/* Monsterrat */
@font-face {
  font-family: "Montserrat";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/montserratregular.woff2") format("woff2"), url("../fonts/montserratregular.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 600;
  font-style: normal;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/montserratsemibold.woff2") format("woff2"), url("../fonts/montserratsemibold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/montserratbold.woff2") format("woff2"), url("../fonts/montserratbold.woff") format("woff");
}
@font-face {
  font-family: "Montserrat";
  font-weight: 900;
  font-style: normal;
  font-display: swap;
  src: local("Montserrat"), url("../fonts/montserratblack.woff2") format("woff2"), url("../fonts/montserratblack.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

body {
  width: 100%;
  height: 100%;
  font-style: normal;
  font-weight: 400;
  font-size: 17px;
  line-height: 21px;
  font-family: "Montserrat", "Arial", sans-serif;
  color: #000000;
  background-color: #ffffff;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
  overflow: hidden;
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0);
}

.container {
  width: 100%;
  max-width: 1288px;
  margin: 0 auto;
  padding: 0 56px;
}
@media (max-width: 1366px) {
  .container {
    padding: 0 16px;
  }
}
@media (max-width: 320px) {
  .container {
    padding: 0 8px;
  }
}

.task-one {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  max-width: 1288px;
  width: 100%;
  min-width: 320px;
  min-height: 592px;
  border-radius: 8px;
  background-color: #f7f1fe;
  margin-top: 32px;
  margin-bottom: 32px;
}
@media (max-width: 1366px) {
  .task-one {
    max-width: 960px;
    min-height: 582px;
  }
}
@media (max-width: 320px) {
  .task-one {
    max-width: 320px;
    padding-top: 10px;
    padding-bottom: 13px;
  }
}
.task-one__category {
  margin-right: 48px;
  margin-top: 20px;
  max-width: 33%;
}
@media (max-width: 1366px) {
  .task-one__category {
    margin-right: 32px;
  }
}
@media (max-width: 320px) {
  .task-one__category {
    margin-right: auto;
    margin-top: 10px;
    margin-bottom: 2px;
  }
}
.task-one__category:last-child {
  margin-right: 0;
}

.task-two {
  --t2-zindex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  max-width: 1288px;
  width: 100%;
  min-width: 320px;
  min-height: 570px;
  border-radius: 16px;
  background-image: linear-gradient(90deg, #f7636b 0%, #fbb1b5 100%);
  margin-top: 32px;
  margin-bottom: 32px;
  padding: 16px;
  overflow: hidden;
  color: #ffffff;
}
@media (max-width: 1366px) {
  .task-two {
    max-width: 960px;
    min-height: 570px;
    padding-top: 10px;
  }
}
@media (max-width: 320px) {
  .task-two {
    max-width: 320px;
    padding: 0;
    padding-top: 2px;
    padding-bottom: 24px;
  }
}
.task-two__upper-part {
  margin-top: 30px;
}
@media (max-width: 320px) {
  .task-two__lower-part {
    overflow: hidden;
  }
}

.category {
  display: flex;
  flex-direction: column;
  max-width: 360px;
}
@media (max-width: 1366px) {
  .category {
    max-width: 288px;
  }
}
@media (max-width: 320px) {
  .category {
    max-width: 304px;
  }
}
.category__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 18px;
  line-height: 24px;
  font-weight: 900;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #f42f3a;
  margin: 0;
  padding: 0;
  margin-top: 11px;
  margin-bottom: 9px;
}
@media (max-width: 1366px) {
  .category__title {
    margin-bottom: 9px;
  }
}
@media (max-width: 320px) {
  .category__title {
    max-width: 304px;
  }
}

.main-article__link:hover, .main-article__link:focus {
  text-decoration: underline;
}
.main-article__link:active {
  opacity: 0.8;
  text-decoration: none;
}
.main-article__img-wrapper {
  width: 360px;
  height: 216px;
}
@media (max-width: 1366px) {
  .main-article__img-wrapper {
    width: 288px;
    height: 182px;
  }
}
@media (max-width: 320px) {
  .main-article__img-wrapper {
    width: 304px;
    height: 182px;
  }
}
@media (max-width: 1366px) {
  .main-article__img-wrapper img {
    width: 288px;
    height: 182px;
  }
}
@media (max-width: 320px) {
  .main-article__img-wrapper img {
    width: 304px;
    height: 182px;
  }
}
.main-article__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  margin: 0;
  padding: 0;
  margin-top: 9px;
  max-width: 360px;
}
@media (max-width: 1366px) {
  .main-article__title {
    max-width: 288px;
    min-height: 72px;
  }
}
@media (max-width: 320px) {
  .main-article__title {
    max-width: 304px;
  }
}

.sub-articles__list {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  list-style: none;
  padding: 0;
  margin: 0;
  margin-top: 15px;
}
.sub-articles__item {
  margin-top: 16px;
  margin-bottom: 16px;
}
.sub-articles__link {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.sub-articles__link:hover, .sub-articles__link:focus {
  text-decoration: underline;
}
.sub-articles__link:active {
  opacity: 0.8;
  text-decoration: none;
}
.sub-articles__img-wrapper {
  min-width: 80px;
  height: 80px;
  margin-right: 16px;
}
@media (max-width: 1366px) {
  .sub-articles__img-wrapper {
    margin-right: 12px;
  }
}
@media (max-width: 320px) {
  .sub-articles__img-wrapper {
    margin-right: 16px;
  }
}
.sub-articles__title {
  font-family: "Georgia", serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;
  margin: 0;
  padding: 0;
  margin-top: 1px;
  max-width: 320px;
}

.upper-part {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
@media (max-width: 320px) {
  .upper-part {
    flex-direction: column;
    padding: 0 10px;
  }
}
.upper-part__description {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  max-width: 288px;
  min-width: 288px;
  margin-right: 24px;
  position: relative;
}
@media (max-width: 1366px) {
  .upper-part__description {
    min-width: 288px;
  }
}
@media (max-width: 320px) {
  .upper-part__description {
    min-height: 150px;
    margin-bottom: 18px;
  }
}
.upper-part__description::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 43px;
  width: 150px;
  height: 4px;
  background-color: #ffffff;
}
@media (max-width: 320px) {
  .upper-part__description::after {
    display: none;
  }
}
.upper-part__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 24px;
  line-height: 32px;
  font-weight: 900;
  letter-spacing: 0.1em;
  text-transform: uppercase;
  color: #ffffff;
  margin: 0;
  padding: 0;
  margin-top: 4px;
  margin-bottom: 10px;
}
@media (max-width: 1366px) {
  .upper-part__title {
    margin-top: 10px;
  }
}
@media (max-width: 320px) {
  .upper-part__title {
    margin-top: 0;
  }
}
.upper-part__counter {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 600;
  text-transform: uppercase;
  margin-top: 15px;
  margin-bottom: auto;
}
@media (max-width: 320px) {
  .upper-part__counter {
    margin-top: 7px;
  }
}
.upper-part__articles {
  margin-top: 4px;
}
@media (max-width: 1366px) {
  .upper-part__articles {
    margin-top: 10px;
  }
}
@media (max-width: 320px) {
  .upper-part__articles {
    margin-top: 0;
  }
}

@property --btn-opacity {
  syntax: "<number>";
  inherits: false;
  initial-value: 0.5;
}
.articles {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  position: relative;
}
.articles__btn {
  width: 48px;
  height: 48px;
  border: none;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, var(--btn-opacity));
  transition: --btn-opacity 0.3s;
  position: absolute;
  content: "";
  top: 62px;
}
.articles__btn::before, .articles__btn::after {
  content: "";
  position: absolute;
  width: 15.56px;
  height: 2px;
  background-color: #ffffff;
}
.articles__btn--prev {
  left: 8px;
}
@media (max-width: 1366px) {
  .articles__btn--prev {
    display: none;
  }
}
.articles__btn--prev::before, .articles__btn--prev::after {
  left: 15px;
}
.articles__btn--prev::before {
  top: 18px;
  transform: rotate(-45deg);
}
.articles__btn--prev::after {
  top: 28px;
  transform: rotate(45deg);
}
.articles__btn--next {
  right: 8px;
}
@media (max-width: 1366px) {
  .articles__btn--next {
    right: 24px;
  }
}
@media (max-width: 320px) {
  .articles__btn--next {
    display: none;
  }
}
.articles__btn--next::before, .articles__btn--next::after {
  left: 17px;
}
.articles__btn--next::before {
  top: 18px;
  transform: rotate(45deg);
}
.articles__btn--next::after {
  top: 28px;
  transform: rotate(-45deg);
}
.articles__btn:hover, .articles__btn:focus {
  --btn-opacity: 0.8;
}
.articles__btn:active {
  --btn-opacity: 0.3;
}
.articles__list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  list-style: none;
  margin: 0;
  padding: 0;
}
@media (max-width: 320px) {
  .articles__list {
    flex-direction: column;
  }
}
.articles__item {
  margin-right: 16px;
  margin-bottom: 24px;
}
@media (max-width: 320px) {
  .articles__item {
    margin-bottom: 13px;
  }
}
.articles__item:last-child {
  margin-right: 0;
}
@media (max-width: 1366px) {
  .articles__item:last-child {
    display: none;
  }
}
@media (max-width: 320px) {
  .articles__item:last-child {
    display: block;
    margin-bottom: 11px;
  }
}
.articles__link {
  transition: text-shadow 0.3s;
}
.articles__link:hover, .articles__link:focus {
  text-shadow: 0 1px 1px #000000;
}
.articles__link:active {
  text-shadow: 0 -1px 1px #000000;
}
.articles__img-wrapper {
  width: 304px;
  height: 172px;
  border-radius: 8px;
  overflow: hidden;
}
@media (max-width: 1366px) {
  .articles__img-wrapper {
    width: 300px;
  }
}
.articles__img-wrapper img {
  object-fit: fill;
  width: 304px;
  height: 172px;
}
@media (max-width: 1366px) {
  .articles__img-wrapper img {
    width: 300px;
  }
}
.articles__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 18px;
  line-height: 24px;
  font-weight: 700;
  color: #ffffff;
  margin: 0;
  padding: 0;
  margin-top: 17px;
  margin-bottom: 10px;
  max-width: 304px;
}

@property --opacity {
  syntax: "<number>";
  inherits: false;
  initial-value: 0.6;
}
.lower-part {
  position: relative;
}
@media (max-width: 320px) {
  .lower-part {
    padding: 0 10px;
  }
}
.lower-part::before {
  content: "";
  position: absolute;
  top: 2px;
  --border-width: calc(100% + 16px);
  width: var(--border-width);
  height: 2px;
  background-image: url("../img/sprite/empty-block.svg");
  background-repeat: repeat;
}
@media (max-width: 1366px) {
  .lower-part::before {
    width: 100%;
  }
}
@media (max-width: 320px) {
  .lower-part::before {
    top: 0;
  }
}
.lower-part__list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  flex-wrap: wrap;
  list-style: none;
  margin: 0;
  padding: 0;
  margin-top: 14px;
  margin-bottom: -16px;
}
@media (max-width: 1366px) {
  .lower-part__list {
    flex-wrap: nowrap;
  }
}
@media (max-width: 320px) {
  .lower-part__list {
    margin-top: 10px;
  }
}
.lower-part__item {
  padding: 16px;
  margin-top: 16px;
  margin-right: 16px;
  margin-bottom: 16px;
  max-width: 408px;
  width: 100%;
  min-height: 172px;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 8px;
  position: relative;
}
@media (max-width: 1366px) {
  .lower-part__item {
    max-width: 456px;
    min-width: 456px;
  }
}
@media (max-width: 320px) {
  .lower-part__item {
    min-width: 220px;
    max-width: 220px;
    margin-right: 8px;
  }
}
.lower-part__item::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  border-radius: 8px;
  background-color: rgba(0, 0, 0, var(--opacity));
  transition: --opacity 0.6s;
}
.lower-part__item:nth-child(3n) {
  margin-right: 0;
}
.lower-part__item:hover::before {
  --opacity: 0.3;
}
.lower-part__item--interviews {
  background-image: url("../img/bg/category-1-initial.jpg");
  background-position: 0 -50px;
}
@media (max-width: 1366px) {
  .lower-part__item--interviews {
    background-position: 0 -67px;
  }
}
@media (max-width: 320px) {
  .lower-part__item--interviews {
    background-position: -19px 0;
  }
}
.lower-part__item--personal-money {
  background-image: url("../img/bg/category-2-initial.jpg");
  background-position: 0 -51px;
}
@media (max-width: 1366px) {
  .lower-part__item--personal-money {
    background-position: 0 -67px;
  }
}
@media (max-width: 320px) {
  .lower-part__item--personal-money {
    background-position: -19px 0;
  }
}
.lower-part__item--weekend-films {
  background-image: url("../img/bg/category-3-initial.jpg");
  background-position: 0 -211px;
}
@media (max-width: 1366px) {
  .lower-part__item--weekend-films {
    background-position: 0 -278px;
  }
}
@media (max-width: 320px) {
  .lower-part__item--weekend-films {
    background-position: -19px 0;
  }
}
.lower-part__link {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-start;
  position: relative;
  width: 100%;
  height: 100%;
  transition: text-shadow 0.3s;
}
.lower-part__link:hover, .lower-part__link:focus {
  text-shadow: 0 1px 1px #000000;
}
.lower-part__link:active {
  text-shadow: 0 -1px 1px #000000;
}
.lower-part__article-counter {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  font-weight: 400;
  color: #ffffff;
  margin-top: 0;
  margin-bottom: auto;
}
@media (max-width: 320px) {
  .lower-part__article-counter {
    margin-top: 1px;
  }
}
.lower-part__title {
  font-family: "Montserrat", "Arial", sans-serif;
  font-size: 24px;
  line-height: 32px;
  font-weight: 700;
  color: #ffffff;
  margin: 0;
  padding: 0;
  margin-top: auto;
  margin-bottom: -1px;
}
@media (max-width: 1366px) {
  .lower-part__title {
    max-width: 340px;
  }
}
@media (max-width: 320px) {
  .lower-part__title {
    font-size: 18px;
    line-height: 24px;
  }
}